import React from 'react';
import UserList from './UserList';
import './BodyRow.css';
import SearchIcon from '@material-ui/icons/Search';
import SmsIcon from '@material-ui/icons/Sms';

function BodyRow() {
    return (
        <div className="Mess__Body__Chat__Row">
            <div className="Search__Messenger">
                <SearchIcon className="Search__Icons"/>{/* Search Icon */}
                <input type="text" placeholder="Search Messenger"/>{/* input Box */}
            </div>
            <ul className="FB__User__Row">
                <li className="newRequests">
                    <SmsIcon className="sms__Ions" />
                    <div className="newRequests__Info">
                        <h4>New Message Requests</h4>
                        <p>Lorem ipsum dolor sit amet.</p>
                    </div>
                </li>
                <div className="FB__Friends__List">
                    <UserList selected />
                    <UserList />
                    <UserList />
                    <UserList />
                    <UserList />
                    <UserList />
                    <UserList />
                    <UserList />
                    <UserList />
                    <UserList />
                    <UserList />
                    <UserList />
                    <UserList />
                    <UserList />
                    <UserList />
                    <UserList />
                    <UserList />
                    <UserList />
                    <UserList />
                    <UserList />
                    <UserList />
                </div>
            </ul>
        </div>
    )
}

export default BodyRow
