import React from 'react';
import './UserChatBoxNewMessage.css';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import GifIcon from '@material-ui/icons/Gif';
import InsertDriveFileIcon from '@material-ui/icons/InsertDriveFile';
import ImageIcon from '@material-ui/icons/Image';
import EmojiEmotionsIcon from '@material-ui/icons/EmojiEmotions';
import ThumbUpIcon from '@material-ui/icons/ThumbUp';


function UserChatBoxNewMessage() {
    return (
        <div className="UserChatBox__NewMessage">
            <AddCircleIcon fontSize="large"/>
            <GifIcon fontSize="large"/>
            <InsertDriveFileIcon fontSize="large"/>
            <ImageIcon fontSize="large"/>
            <span className="inputArea">
                <input className="ChatBox" placeholder="Type a message..."></input>
                <EmojiEmotionsIcon fontSize="large"/>
            </span>
            <ThumbUpIcon fontSize="large"/>
        </div>
    )
}

export default UserChatBoxNewMessage

