import React from 'react';
import './MesseagerUserCard.css';
import Avatar from '@material-ui/core/Avatar';

function MesseagerUserCard({title, timestamp, UserMess, image, seenIcon}) {
    return (
        <div className="MesseagerUserCard__Data">
            <li>
                <img src={image} alt={title}/>
                <div className="UserRowList__Info">
                    <h4>{title}</h4>
                    <p>{UserMess} <span>{timestamp}</span></p>
                    {/* <img className="userSeenImg" src={seenIcon} alt=""/> */}
                </div>
            </li>
        </div>
    )
}

export default MesseagerUserCard
