import React from 'react';
import './Body.css';
import BodyRow from './BodyRow';
import BodyRowBox from './BodyRowBox';

function Body() {
    return (
        <div className="App__Body">
            <div className="Body__LeftRow">
                <BodyRow />
            </div>
            <div className="Body__RightRow__Box">
                <BodyRowBox />
            </div>
        </div>
    )
}

export default Body
