import React from 'react';
import './BodyRowBox.css';
import UserChatBox from './UserChatBox';
import UserChatBoxInfo from './UserChatBoxInfo';

function BodyRowBox() {
    return (
        <div className="UserBodyRow__Area">
            <div className="UserChatBox__Row">
                <UserChatBox />
            </div>
            <div className="UserChatBoxInfo__Row">
                <UserChatBoxInfo />
            </div>
        </div>
    )
}

export default BodyRowBox
