import React from 'react';
import './UserChatBox.css';
import UserChatBoxMessage from './UserChatBoxMessage';
import UserChatBoxNewMessage from './UserChatBoxNewMessage';

function UserChatBox() {
    return (
        <div className="UserChatBox__ChatData">
            <div className="UserChatBoxMessage__Data">
                <UserChatBoxMessage />
            </div>
            <div className="UserChatBoxNewMessage__Data">
                <UserChatBoxNewMessage />
            </div>
        </div>
    )
}

export default UserChatBox
